<template>
  <div class="container">
    <h2 class="text-center">系統管理</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.go(-1)">返回上一頁</button>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <div class="row">
      <div class="col-6 d-grid gap-2 my-2">
        <button class="btn btn-success btn-lg" type="button" @click="$router.push('/accountManage')">帳戶管理</button>
      </div>
      <div class="col-6 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="$router.push('/questionManage')">題庫管理</button>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-info btn-lg" type="button" @click="$router.push('/countDownEdit')">設定倒數時間</button>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-danger btn-lg" type="button" @click="resetDB">資料庫初始化</button>
      </div>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";

export default {
  name: "SystemManage",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: 0,
    };
  },
  methods: {
    resetDB() {
      // 訊息提示框
      this.alertVisible = true;
      this.MSG.MODE = "OKCancel"; // 形式為：OKOnly、OKCancel
      this.funcNum = 1;
      this.MSG.TYPE = "warning";
      this.MSG.TITLE = "系統提示";
      this.MSG.TEXT = ["確定要資料庫初始化？"];
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  mounted() {},
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === -1) {
            this.hideAlert();
          } else if (this.funcNum === -2) {
            this.hideAlert();
            this.$router.go(-1);
          } else if (this.funcNum === 1) {
            // 資料庫初始化
            this.$http
              .post("api/systemManage/resetDatabase")
              .then((res) => {
                if (res.data.code === 200) {
                  // 訊息提示框
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
                  this.funcNum = 2;
                  this.MSG.TYPE = "primary";
                  this.MSG.TITLE = "系統提示";
                  this.MSG.TEXT = [];
                  this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                  this.MSG.TEXT = ["資料庫初始化。", "請重新登入。"];
                } else {
                  // 訊息提示框
                  this.alertVisible = true;
                  this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
                  this.funcNum = -1;
                  this.MSG.TYPE = "danger";
                  this.MSG.TITLE = "系統錯誤";
                  this.MSG.TEXT = [];
                  this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                  if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
                }
              })
              .catch((err) => {
                // 訊息提示框
                this.alertVisible = true;
                this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
                this.funcNum = -1;
                this.MSG.TYPE = "danger";
                this.MSG.TITLE = "系統錯誤";
                this.MSG.TEXT = [];
                this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
                if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
              });
          } else if (this.funcNum === 2) {
            // 資料庫初始化-後續處理
            this.hideAlert();
            this.$router.push("/");
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
